import styled from "styled-components";

export const ResetButton = styled.button`
  display: block;
  margin: 0 auto;
  background-color: #fca311;
  padding: 0.2rem 0.8rem;
  width: 150px;
  border: solid 2px #14213d;
  border-radius: 10px;
  font-weight: bolder;
  cursor: pointer;
`;

export const StartButton = styled.button`
  display: block;
  width: 200px;
  margin: 0 auto;
  padding: 1rem;
  background-color: #fca311;
  border: solid 2px #14213d;
  border-radius: 10px;
  font-weight: bolder;
  cursor: pointer;
`;
